import React, {useState , useEffect} from 'react'

export default function ForgetPassword() {
    const [userEmail , setUserEmail] = useState(""); 

    useEffect(() => {
        document.title = "Forgrt Password | Travisit";
    });

    return (
        <div className =" form-page">
            <section className =" container shadwo text-white ">
                <div className = "row section-content">
                    <div className = "col-lg-6  font-weight-bold">
                        <h3>Forget <br/> password?</h3>
                        <div className=" text-right">
                            <img className="img-fluid mt-lg-0 mt-5" src = "images/Thinking.svg" alt="travisit"/>
                        </div>
                    </div>

                    <form method="" className = "col-lg-6  mb-auto mt-auto pt-4">

                        <div className =" form-group">
                            <label>Email Address</label>
                            <input onChange={(e)=>setUserEmail(e.target.value)} type ="email" name ="email" placeholder="ex: example@gmail.com" className=" form-control"/>
                        </div>
                        
                        <input type="submit" name ="reset-password" value="Reset Password" className=" btn btn-submit"/>  

                    </form>
                    
                </div>
            </section>
        </div>
    )
}
