import React, { Component } from "react";
import { Link } from "react-router-dom";
import Joi ,{joi} from "joi-browser";
import { toast } from "react-toastify";

import "./form.css";

export default class SignUpForm extends Component {
  state = {
    rull: "",
    showPass: true,
    name: "",
    email: "",
    passWord: "",
    stage:0,
    errors: {},
  };

  schema = {
    rull:     Joi.string().required(),
    passWord: Joi.string().required(),
    name:     Joi.string().min(3).max(50).required(),
    email:    Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }).required(),
  };

  handleSubmit = (e) => {
    e.preventDefault();

    //console.log(this.state)
    const errors = this.validate();

    
    if (errors) return errors;

    //Call Backend
    console.log("submit");
    
    switch(this.state.rull){
      case "Traveler"|| "Tour Guide" :
        this.props.history.replace(`wellcome-page/${this.state.name}`);
        toast.success("submited successfuly");
        break;
      case "Agancy" :
        this.props.history.replace("complet-data");
        toast.info("Complet Data");
        break;
      default:
        toast.error("can't submit");
    }
  };

  validate = () => {
    const errors = {};
    //Clone State
    const state = { ...this.state };
    delete state.errors;
    delete state.showPass;
    delete state.stage;
    console.log("New State:", state);
    const res = Joi.validate(state, this.schema, { abortEarly: false });
    if (res.error === null) {
      this.setState({ errors: {} });
      return null;
    }

    for (const error of res.error.details) {
      errors[error.path] = error.message;
    }

    //Set State
    this.setState({ errors });
    return errors;
  };

  componentDidMount () {
    document.title = "SinUp | Travisit";
  }

  render() {
    return (
      <div className=" form-page">
        <section className=" container shadwo text-white ">
          <div className="row section-content">
            <div className="col-lg-6  font-weight-bold">
              <h3 style={{
                width:"250px",
                }}>
                Hello <span  className="text-dark">{" " + this.state.name}</span>{" "}
              </h3>
              <h2>Sign Up!</h2>
              <div className="logo text-center">
                <img src="images/logoOnly.svg" alt="travisit" />
                <h1 className="mt-2">TRAVISIT</h1>
              </div>
            </div>

            <form onSubmit={this.handleSubmit} className="col-lg-6 ">
              <div className=" form-group">
                <label>singn up as:</label>
                <select
                  onClick = {(e) => this.setState({rull : e.currentTarget.value})}
                  name="rull"
                  className=" form-control" >
                  <option value = "0" >       select one</option>
                  <option value="Traveler">     Traveler</option>
                  <option value="Tour Guide"> Tour guide</option>
                  <option value="Agancy">         Agancy</option>
                </select>
                {this.state.errors.rull && (
                  <div className=" alert alert-danger p-1 ">
                    {this.state.errors.rull}
                  </div>
                )}
              </div>
              <div className=" form-group">
                <label>Full Name:</label>
                <input
                  onChange={(e) =>{ 
                    if(e.currentTarget.value.length <= 50){
                      this.setState({ name: e.currentTarget.value })
                    }
                    }}
                  type="text"
                  name="name"
                  placeholder="full name"
                  className=" form-control"
                />
                {this.state.errors.name && (
                  <div className=" alert alert-danger p-1 ">
                    {this.state.errors.name}
                  </div>
                ) }
              </div>
              
              <div className=" form-group">
                <label>Email Address</label>
                <input
                  onChange={(e) => this.setState({ email: e.currentTarget.value })}
                  type="email"
                  name="email"
                  placeholder="ex: example@gmail.com"
                  className=" form-control"
                />
                {this.state.errors.email && (
                  <div className=" alert alert-danger p-1 ">
                    {this.state.errors.email}
                  </div>
                ) }
              </div>
              <div className=" form-group password-group">
                {this.state.showPass === true ? (
                  <i
                    onClick={() =>
                      this.state.showPass === true
                        ? this.setState({ showPass: false })
                        : this.setState({ showPass: true })
                    }
                    className=" fal fa fa-eye-slash "
                  ></i>
                ) : (
                  <i
                    onClick={() =>
                      this.state.showPass === true
                        ? this.setState({ showPass: false })
                        : this.setState({ showPass: true })
                    }
                    className=" fal fa fa-eye text-main"
                  ></i>
                )}
                <label>password:</label>
                <input
                  onChange={(e) => this.setState({ passWord: e.currentTarget.value })}
                  type={this.state.showPass === true ? "password" : "text"}
                  name="password"
                  placeholder="*********"
                  className=" form-control"
                />
                {this.state.errors.passWord && (
                  <div className=" alert alert-danger p-1 ">
                    {this.state.errors.passWord}
                  </div>
                ) }
              </div>

              <button type="submit" className=" btn btn-submit">
                Submit
              </button>

              <p className=" mt-4 text-center text-lg-left">
                Already have account?{" "}
                <Link to="/sign-in"  className=" text-main ">
                  Sign in
                </Link>
              </p>
            </form>
          </div>
        </section>
      </div>
    );
  }
}
