import React, { Component } from "react";
import { Link } from "react-router-dom";
import Joi from "joi-browser";
import { toast } from "react-toastify";



export default class SignInForm extends Component {

  state = {
    rull: "",
    email: "",
    password: "",
    showPass: true,
    errors: {
      rull:"",
    }
  };

  schema = {
    rull:     Joi.string().required(),
    email:    Joi.string().required(),
    password: Joi.string().required(),
  };

  handleSubmit = (e) => {
    e.preventDefault();

    const errors = this.validate();

    if (errors) return ;

    //Call Backend
    console.log(this.state);
    this.props.history.replace("/wellcome-page");
    toast.success("Loged in Successfuly");
  
  };

  validate = () => {
    const errors = {};
    //Clone State
    const state = { ...this.state };
    delete state.errors;
    delete state.showPass;
    console.log("New State:", state);
    const res = Joi.validate(state, this.schema, { abortEarly: false });
    if (res.error === null) {
      this.setState({ errors: {} });
      return null;
    }

    for (const error of res.error.details) {
      errors[error.path] = error.message;
    }

    //Set State
    this.setState({ errors });
    return errors;
  };

  handleChange = (e) => {
    let state = {...this.state};
    console.log("changed");
    state[e.currentTarget.name] = e.currentTarget.value;

    this.setState(state)
  };

  componentDidMount () {
    document.title = "Sinin | Travisit";
  }

  render() {
    return (
      <div className=" form-page">
        <section className=" container shadwo text-white ">
          <div className="row section-content">
            <div className="col-lg-6  font-weight-bold">
              <h4>Welcome Back</h4>
              <h2>Sign In!</h2>
              <div className="logo text-center">
                <img src="images/logoOnly.svg" alt="travisit" />
                <h1 className="mt-2">TRAVISIT</h1>
              </div>
            </div>

            <form onSubmit={this.handleSubmit}  className="col-lg-6 ">
              <div className=" form-group">
                <label htmlFor="rull">singn in as:</label>
                <select onChange = {this.handleChange} name = "rull" value={this.state.rull} id="rull" className=" form-control">
                  <option value = "noAvilable">select one</option>
                  <option value = "Traveler">Traveler</option>
                  <option value = "Agancy">Agancy</option>
                  <option value = "Tour guide">Tour guide</option>
                </select>
                {this.state.errors.rull && (
                    <div className=" alert-danger p-1 mt-1">
                        {this.state.errors.rull}
                    </div>
                )}
              </div>

              <div className=" form-group">
                <label htmlFor="email">Email Address</label>
                <input
                    name="email"
                    value = {this.state.email}
                    onChange = {this.handleChange}
                    id = "email"
                    type="email"
                    placeholder="ex: example@gmail.com"
                    className=" form-control"
                />
                {this.state.errors.email && (
                    <div className=" alert-danger p-1 mt-1">
                        {this.state.errors.email}
                    </div>
                )}
              </div>
              <div className=" form-group password-group">
                {this.state.showPass === true ? (
                  <i
                    onClick={() =>
                      this.state.showPass === true
                        ? this.setState({ showPass: false })
                        : this.setState({ showPass: true })
                    }
                    className=" fal fa fa-eye-slash "
                  ></i>
                ) : (
                  <i
                    onClick={() =>
                      this.state.showPass === true
                        ? this.setState({ showPass: false })
                        : this.setState({ showPass: true })
                    }
                    className=" fal fa fa-eye text-main"
                  ></i>
                )}
                <label htmlFor="password">password:</label>
                <input 
                    name="password"
                    value = {this.state.password}
                    onChange = {this.handleChange}
                    id="password"
                    type={this.state.showPass === true ? "password" : "text"}
                    placeholder="***********"
                    className=" form-control"
                />

                {this.state.errors.password && (
                    <div className=" alert-danger p-1 mt-1">
                        {this.state.errors.password}
                    </div>
                )}
                
                <small className="text-right d-block pr-3 pt-3">
                  <Link to = "/forget-password" className="text-dark">
                    forget password?
                  </Link>
                </small>
              </div>

              <input
                type="submit"
                name="sign-In"
                value="Sign In"
                className=" btn btn-submit"
              />

              <p className=" mt-4 text-center text-lg-left">
                Not have account?
                <Link to="/sign-up" className=" text-main  btn btn-link">
                  Sign up
                </Link>
              </p>
            </form>
          </div>
        </section>
      </div>
    );
  }
}
