import React, { Component } from 'react';





export default class CompletData extends Component {
    state = {
        BusinessName:"",
        selectedcat:{},
        logUrl:"",
        GovImgUrl:"",
        GovNumb:"",
        categories:[
            "Advanture",
            "diving",
            "copming",
            "hiking",
            "cruise",
            "Ana MO",
            "Travisit"
        ]
    }

    handleCatClick = (e) => {
        e.preventDefault();

        if(e.target.classList.contains("bg-info")){
            e.currentTarget.classList.remove("bg-info","text-white");
            let cats = {...this.state.selectedcat};
        }else {
            e.currentTarget.classList.add("bg-info","text-white");
            let cats = {...this.state.selectedcat,[e.target.value]:e.target.value};
            this.setState({selectedcat:cats});
        }
        

        
        console.log(this.state.selectedcat);

        
    }

    componentDidMount () {
        document.title = "Complet Data | Travisit";
    }
    
    render() {
        return (
            <div className =" form-page">
            <section className =" container shadwo text-white ">
                <div className = "row ">
                    <div className = "col-lg-6 pt-3  font-weight-bold">
                        <h4>Welcome Name </h4>
                        <h3>Complete Profile</h3>
                        <div className="logo text-center">
                            <img src = "images/logoOnly.svg" alt="travisit"/>
                            <h1 className = "mt-2">TRAVISIT</h1>
                        </div>
                    </div>

                    <form method="" className = "col-lg-6 ">
            
                        <div className =" form-group">
                            <label>Business Name</label>
                            <input type ="text" name ="BusinessName" placeholder="ex: TRAVISIT" className=" form-control"/>
                        </div>
                        <div className =" form-group">
                            <label>Categories</label>
                            <div className="col-10">

                                {this.state.categories.map(cat => (
                                    <input onClick = {this.handleCatClick} type="button" name={cat} value={cat} style={{borderRadius:"25px"}} className="btn btn-outline-info mr-2 mb-2"/>
                                ))}
                                
                            </div>
                        </div>
                        <div className =" form-group password-group">
                            <label>Government Issued Number:</label>
                            <input type="file"/>
                            <input type ="text" name ="GovtNumb" placeholder="2001-2355-9107" className=" form-control"/>
                        </div>

                        <input type="submit" name ="Continue" value="Continue" className=" btn btn-submit"/>  

                    </form>
                    
                </div>
            </section>
        </div>
        )
    }
}
