import React, { useEffect } from "react";
import Header from "../components/Header";
import Aos from "aos";
import AboutTravist from "./AboutTravist";
import OurFeatur from "./OurFeatur";
import OurTeam from "./OurTeam";
import OurPartners from "./OurPartners";

function Home() {
  const vedioFrame = () => (
    <div>
      <div className="video-frame d-flex justify-content-center align-items-center hidden">
        <iframe
          src="https://www.youtube.com/embed/pkcyLzEcENo"
          frameBorder="0"
          allow="accelerometer;  encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
      </div>
    </div>
  );

  const vedioBtn = () => {
    // videoFrame.classList.toggle('hidden');
    // this.style.overflow = 'hidden';
    console.log("this value", this);
  };

  useEffect(() => {
    Aos.init({ duration: 2000 });
    document.title = "Travisit";
  }, []);

  return (
    <>
      <Header />
      <main style={{ overflow: "hidden" }}>
        <AboutTravist />
        <OurFeatur />
        <OurTeam />
        <OurPartners />
      </main>
    </>
  );
}

export default Home;
