import React from 'react';
import { Link, NavLink } from "react-router-dom";
//import logo from '%PUBLIC_URL%/images/coloedLOGO.svg';


function NavbarLink(props) {
    return (
        <>
            <li className ="nav-item ">
                <NavLink className ={`nav-link ${props.addClassName}`} to={props.to} >{props.linkName} </NavLink>
            </li>
        </>
    )
}



function Navbar() {
    return (
        <>
            <nav className ="navbar navbar-expand-lg navbar-light fixed-top">
                <div className ="container">
                    <Link className ="navbar-brand" to="/home">
                        <img src={process.env.PUBLIC_URL + "/images/coloedLOGO.svg"} className ="d-inline-block align-top" alt="" />
                    </Link>
        
                    <button className ="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <i className ="fas fa-stream"></i>
                    </button>
                    
                    <div className ="collapse navbar-collapse" id="navbarNav">
                        <ul className ="navbar-nav ml-auto">
                            <NavbarLink linkName = "Home"     to = "/"/>
                            <NavbarLink linkName = "About us" to = "#about"/>
                            <NavbarLink linkName = "Features" to = "#features"/>
                            <NavbarLink linkName = "Team"     to = "#team"/>
                            <NavbarLink linkName = "Contact"  to = "#contact"/>
                        </ul>
                        <Link className="nav-link ml-3 px-3 py-2 btn btn-submit" to="/sign-up">Sign Up</Link>
                    </div>
                </div>
            </nav>
        </>
        );
    }

export default Navbar;