import React , {useEffect } from 'react';
import Aos from "aos";

function Header() {
    useEffect(() => {
        Aos.init({duration: 2000});
    }, []);

    return (
        <header  className="text-white" id="home">
            <div className="center text-lg-center ">
                <h1 data-aos="zoom-in" >Step into your <span className=" text-main"> Journey </span> </h1>
                <p data-aos="fade-up" className="mb-4 mt-3 ">
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Quasi, molestias.
                </p>
                <button data-aos="fade-up" className="btn bg-main px-4 ">
                    Adventure
                    <i className="ml-1  fas fa-play "></i>
                </button>
            </div>
            <div className="social">
                <ul>
                    <li data-aos="fade-left"> <a href="#" className="fab fa-facebook-f" ></a> </li>
                    <li data-aos="fade-left"> <a href="#" className="fab fa-twitter"    ></a> </li>
                    <li data-aos="fade-left"> <a href="#" className="fab fa-instagram"  ></a> </li>
                    <li data-aos="fade-left"> <a href="#" className="fab fa-linkedin-in"></a> </li>
                </ul>
            </div>
        </header>
    );
}

export default Header;