import React from 'react';

// this librerary coming from 'npm i react-router-dom' commend
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from 'react-router-dom';

import { ToastContainer } from 'react-toastify';


import "aos/dist/aos.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-toastify/dist/ReactToastify.css'
import './App.css';
import Home from './pages/home/Home';
import Navbar from './pages/components/Navbar';
import SignUpForm from './pages/form/SignUpForm';
import Footer from './pages/components/Footer';
import SignInForm from './pages/form/SignInForm';
import ForgetPassword from './pages/form/ForgetPassword';
import CompletData from './pages/form/CompletData';
import WellcomePage from './pages/Main App/WellcomePage';
import PageNotFound from './pages/PageNotFound/PageNotFound';

function App() {
  return (
    <div className="App">
      <Router>
        <Navbar />
        <ToastContainer />
          <Switch> {/* instead of exact keyword */}
            <Route  path = '/sign-up'         component = {SignUpForm} />
            <Route  path = '/sign-in'         component = {SignInForm} />
            <Route  path = '/complet-data'    component = {CompletData} />
            <Route  path = '/forget-password' component = {ForgetPassword} />
            <Route  path = '/page-not-found' component = {PageNotFound} />

            <Route  path='/wellcome-page/:name'   render = {(props) => (
              <WellcomePage
                title = "welcomePage | Travisit" 
                {...props}
              />
            )} />

            <Route  path='/home' exact component={Home}/> {/*must be at the last*/}
            <Redirect from="/" exact to="/home"/>

            <Redirect to="/page-not-found"/>
          </Switch>
        <Footer />
      </Router>
      
    </div>
  );
}

export default App;
